export class PaymentGatewayViewController extends BaseViewController {
	constructor( options = {} ) {
		_.defaults( options, {
			el: '#payment_gateway_view_container',

			status_array: null
		} );

		super( options );
	}

	init( options ) {
		//this._super('initialize', options );
		this.edit_view_tpl = 'PaymentGatewayEditView.html';
		this.permission_id = 'payment_gateway';
		this.viewId = 'PaymentGateway';
		this.script_name = 'PaymentGatewayView';
		this.table_name_key = 'payment_gateway';
		this.context_menu_name = $.i18n._( 'Payment Gateway' );
		this.navigation_label = $.i18n._( 'Payment Gateway' );
		this.api = TTAPI.APIPaymentGateway;

		this.render();
		this.buildContextMenu();

		this.initData();
	}

	initOptions( callBack ) {
		var $this = this;
		var options = [
			{ option_name: 'type', field_name: null, api: null },
			{ option_name: 'status', field_name: null, api: null },
			{ option_name: 'credit_card_type', field_name: null, api: null },
			{ option_name: 'bank_account_type', field_name: null, api: null },
			{ option_name: 'driver', field_name: null, api: null }

		];

		this.initDropDownOptions( options, function( result ) {

			if ( callBack ) {
				callBack( result ); // First to initialize drop down options, and then to initialize edit view UI.
			}

		} );
	}

	buildEditViewUI() {

		super.buildEditViewUI();

		var $this = this;

		var tab_model = {
			'tab_payment_gateway': {
				'label': $.i18n._( 'Payment Gateway' )
			},
			'tab_audit': true,
		};
		this.setTabModel( tab_model );

		this.navigation.AComboBox( {
			api_class: TTAPI.APIPaymentGateway,
			id: this.script_name + '_navigation',
			allow_multiple_selection: false,
			layout_name: 'global_payment_gateway',
			navigation_mode: true,
			show_search_inputs: true
		} );

		this.setNavigation();

		//Tab 0 start

		var tab_payment_gateway = this.edit_view_tab.find( '#tab_payment_gateway' );

		var tab_payment_gateway_column1 = tab_payment_gateway.find( '.first-column' );

		this.edit_view_tabs[0] = [];

		this.edit_view_tabs[0].push( tab_payment_gateway_column1 );

		// Status
		var form_item_input = Global.loadWidgetByName( FormItemType.COMBO_BOX );
		form_item_input.TComboBox( { field: 'status_id', set_empty: false } );
		form_item_input.setSourceData( $this.status_array );
		this.addEditFieldToColumn( $.i18n._( 'Status' ), form_item_input, tab_payment_gateway_column1, '' );

		// Name

		form_item_input = Global.loadWidgetByName( FormItemType.TEXT_INPUT );

		form_item_input.TTextInput( { field: 'name', width: '100%' } );
		this.addEditFieldToColumn( $.i18n._( 'Name' ), form_item_input, tab_payment_gateway_column1 );

		//SEPARATED_BOX
		form_item_input = Global.loadWidgetByName( FormItemType.SEPARATED_BOX );
		form_item_input.SeparatedBox( { label: $.i18n._( 'Selection Criteria' ) } );
		this.addEditFieldToColumn( null, form_item_input, tab_payment_gateway_column1 );

		// Type
		form_item_input = Global.loadWidgetByName( FormItemType.COMBO_BOX );
		form_item_input.TComboBox( { field: 'type_id', set_empty: false } );
		form_item_input.setSourceData( $this.type_array );
		this.addEditFieldToColumn( $.i18n._( 'Type' ), form_item_input, tab_payment_gateway_column1, '' );

		//Credit Card Type
		form_item_input = Global.loadWidgetByName( FormItemType.AWESOME_BOX );

		form_item_input = form_item_input.AComboBox( {
			field: 'credit_card_type_id',
			set_empty: true,
			allow_multiple_selection: true,
			layout_name: 'global_option_column',
			key: 'value'
		} );

		form_item_input.setSourceData( $this.credit_card_type_array );
		this.addEditFieldToColumn( $.i18n._( 'Credit Card Type' ), form_item_input, tab_payment_gateway_column1, '', null, true );

		//Bank Account Type
		form_item_input = Global.loadWidgetByName( FormItemType.AWESOME_BOX );

		form_item_input = form_item_input.AComboBox( {
			field: 'bank_account_type_id',
			set_empty: true,
			allow_multiple_selection: true,
			layout_name: 'global_option_column',
			key: 'value'
		} );

		form_item_input.setSourceData( $this.bank_account_type_array );
		this.addEditFieldToColumn( $.i18n._( 'Bank Account Type' ), form_item_input, tab_payment_gateway_column1, '', null, true );

		//Currency
		form_item_input = Global.loadWidgetByName( FormItemType.AWESOME_BOX );

		form_item_input.AComboBox( {
			api_class: TTAPI.APICurrency,
			allow_multiple_selection: true,
			layout_name: 'global_currency',
			show_search_inputs: true,
			set_empty: true,
			field: 'currency_id'
		} );
		this.addEditFieldToColumn( $.i18n._( 'Currency' ), form_item_input, tab_payment_gateway_column1 );

		// Priority

		form_item_input = Global.loadWidgetByName( FormItemType.TEXT_INPUT );
		form_item_input.TTextInput( { field: 'priority', width: 40 } );
		this.addEditFieldToColumn( $.i18n._( 'Priority' ), form_item_input, tab_payment_gateway_column1 );

		//SEPARATED_BOX
		form_item_input = Global.loadWidgetByName( FormItemType.SEPARATED_BOX );
		form_item_input.SeparatedBox( { label: $.i18n._( 'Gateway Credentials' ) } );
		this.addEditFieldToColumn( null, form_item_input, tab_payment_gateway_column1 );

		// Driver
		form_item_input = Global.loadWidgetByName( FormItemType.COMBO_BOX );
		form_item_input.TComboBox( { field: 'driver_id' } );
		form_item_input.setSourceData( $this.driver_array );
		this.addEditFieldToColumn( $.i18n._( 'Driver' ), form_item_input, tab_payment_gateway_column1, '' );

		// Customer ID

		form_item_input = Global.loadWidgetByName( FormItemType.TEXT_INPUT );
		form_item_input.TTextInput( { field: 'customer_id' } );
		this.addEditFieldToColumn( $.i18n._( 'Customer ID' ), form_item_input, tab_payment_gateway_column1 );

		// User Name

		form_item_input = Global.loadWidgetByName( FormItemType.TEXT_INPUT );
		form_item_input.TTextInput( { field: 'user_name' } );
		this.addEditFieldToColumn( $.i18n._( 'User Name' ), form_item_input, tab_payment_gateway_column1 );

		// Password

		form_item_input = Global.loadWidgetByName( FormItemType.TEXT_INPUT );
		form_item_input.TTextInput( { field: 'password' } );
		this.addEditFieldToColumn( $.i18n._( 'Password' ), form_item_input, tab_payment_gateway_column1, '' );
	}

	onTypeChange() {

		if ( this.current_edit_record['type_id'] == 30 ) {

			this.attachElement( 'credit_card_type_id' );
			this.detachElement( 'bank_account_type_id' );

		} else {
			this.detachElement( 'credit_card_type_id' );
			this.attachElement( 'bank_account_type_id' );
		}

		this.editFieldResize();
	}

	//Don't call super if override this function.
	onFormItemChange( target, doNotValidate ) {
		this.setIsChanged( target );
		this.setMassEditingFieldsWhenFormChange( target );
		var key = target.getField();
		this.current_edit_record[key] = target.getValue();

		if ( key === 'type_id' ) {
			this.onTypeChange();
		}

		if ( !doNotValidate ) {
			this.validate();
		}
	}

	setEditViewDataDone() {
		super.setEditViewDataDone();
		this.onTypeChange();
	}

	buildSearchFields() {

		super.buildSearchFields();
		this.search_fields = [

			new SearchField( {
				label: $.i18n._( 'Name' ),
				in_column: 1,
				field: 'name',
				multiple: true,
				basic_search: true,
				form_item_type: FormItemType.TEXT_INPUT
			} ),

			new SearchField( {
				label: $.i18n._( 'Type' ),
				in_column: 1,
				field: 'type_id',
				multiple: true,
				basic_search: true,
				layout_name: 'global_option_column',
				form_item_type: FormItemType.AWESOME_BOX
			} ),

			new SearchField( {
				label: $.i18n._( 'Status' ),
				in_column: 1,
				field: 'status_id',
				multiple: true,
				basic_search: true,
				layout_name: 'global_option_column',
				form_item_type: FormItemType.AWESOME_BOX
			} ),

			new SearchField( {
				label: $.i18n._( 'Created By' ),
				in_column: 2,
				field: 'created_by',
				layout_name: 'global_user',
				api_class: TTAPI.APIUser,
				multiple: true,
				basic_search: true,
				adv_search: false,
				form_item_type: FormItemType.AWESOME_BOX
			} ),

			new SearchField( {
				label: $.i18n._( 'Updated By' ),
				in_column: 2,
				field: 'updated_by',
				layout_name: 'global_user',
				api_class: TTAPI.APIUser,
				multiple: true,
				basic_search: true,
				adv_search: false,
				form_item_type: FormItemType.AWESOME_BOX
			} )
		];
	}

}
